<template>
  <label class="zoom-radio"><span class="zoom-radio__input" :class="isChecked? 'is-checked' : ''"><span class="zoom-radio__inner" @click="isCheckedClick"></span>
    <input ref="isRadio" type="radio" aria-hidden="true" class="zoom-radio__original" :name="checkname" :value="checck" :checked="isChecked?true:false">
    </span><span class="zoom-radio__label"><slot></slot></span>
 </label>
</template>
<script>
export default {
  name: "zoom-radio",
  props: {
      checck: String,
      checkname: String,
      op: {
      }
  },
  data() {
      return {
          isChecked: false,
      }
  },
  created() {
    //   console.log(this.checkname, '子组件');
  },
  methods: {
      isCheckedClick() {
          this.isChecked = true;
          this.$refs['isRadio'].click()
        //   let checkname = document.getElementsByName(this.checkname);
        //   for (var i = 0; i<= checkname.length; i++) {
        //       checkname[i].value = this.$refs['isRadio'].value;
        //   }
          console.log('checkname', document.getElementsByName(this.checkname));
        //   console.log(document.getElementsByName('sex')[0].value);
      }
  }
};
</script>
<style>
/* active */
.zoom-radio__input.is-checked .zoom-radio__inner {
    border-color: #409eff;
    background: #409eff;
}
.zoom-radio__input.is-checked .zoom-radio__inner:after {
    transform: translate(-50%,-50%) scale(1);
}
/* 普通状态 */
.zoom-radio {
    color: #606266;
    font-weight: 500;
    line-height: 1;
    position: relative;
    cursor: pointer;
    display: inline-block;
    white-space: nowrap;
    outline: none;
    font-size: 14px;
    margin-right: 30px;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}
.zoom-radio__input {
    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: middle;
}
.zoom-radio__inner {
    border: 1px solid #dcdfe6;
    border-radius: 100%;
    width: 14px;
    height: 14px;
    background-color: #fff;
    position: relative;
    cursor: pointer;
    display: inline-block;
    box-sizing: border-box;
}
.zoom-radio__inner:after {
    width: 4px;
    height: 4px;
    border-radius: 100%;
    background-color: #fff;
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%) scale(0);
    transition: transform .15s ease-in;
}
.zoom-radio__original {
    opacity: 0;
    outline: none;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0;
}
.zoom-radio__label {
    font-size: 14px;
    padding-left: 10px;
}
</style>